/* Generic Layout Classes*/

body {
  /*width: 99vw;*/
  /*min-height: 100vh;*/
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.flex {
  display: flex;
}

.column {
  flex-direction: column;
}

.row {
  flex-direction: row;
}

.row-wrap {
  flex-flow: row wrap;
  justify-content: space-around;
}

.flex-start {
  justify-content: flex-start;
  align-items: center;
}

.align-start {
  align-items: flex-start;
}

.center-self {
  align-self: center;
}

.space-around {
  justify-content: space-around;
  align-items: center;
}

.space-between {
  justify-content: space-between;
  align-items: center;
}

.center {
  justify-content: center;
  align-items: center;
}

.container-light {
  width: 30%;
  max-width: 350px;
  min-width: 300px;
  border: 1px solid black;
  padding: 1em 1.5em;
  height: 400px;
}

.container-xxl {
  width: 100%;
}

.profile-main {
  width: 850px;
  margin-top: 5em;
}

.dest-menu, .select-menu {
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.instructions {
  text-align: center;
  width: 80%;
}

.inactive {
  color: #ABABAB;
  font-style: italic;
  border: 2px solid #ABABAB;
  height: calc(100vh - 120px);
  width: 80%;
  min-width: 350px;
  /*margin: 2em 2em;*/
  position: relative;
  padding-bottom: 10px;
}

input, select {
  margin: 1em 0.5em 1em 0;
  width: 280px;
  border: 2px solid LightGray;
  font-size: 16px;
  margin-top: 8px;
  margin-bottom: 8px;
}

input {
  border-radius: 6px;
  padding: 3px;
}

label {
  margin: 0.25em auto;
  font-size: 16px;
}

input:focus, select:focus {
  outline: none;
  border-color: #22CCEB;
  transition: 0.3s;
}

.vhr {
  height: 400px;
  width: 2px;
  background-color: LightGray;
  margin: 0 48px;
}

#logo {
  height: 50px;
  width: auto;
  margin-left: 0.5em;
}

#title {
  margin-top: 0px;
  margin-bottom: 0px;
  margin-right: 0.5em;
}

.menu {
  /*border: 2px solid black;*/
  min-height: 250px;
  min-width: 300px;
  /*margin: 0 2em;*/
  position: relative;
}

.no-border {
  border: none;
}

.menu-item {
 width: 300px;
 min-height: 2.5em;
}

.menu-item-checkbox {
  width: 12px;
  margin-left: 0.5em;
}

#template_list {
  /*width: 100%;
  min-width: 650px;*/
  width: 650px;
  /*height: 95vh;*/
  height: 100%;
  min-height: 450px;
  align-self: flex-start;
}

#template_list::-webkit-scrollbar {
  border-radius: 10px;
  width: 10px;
}


/*#template_list::-webkit-scrollbar-track {
  background: #000000;
  border-radius: 10px;
}*/

#template_list::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

.template-list-item {
  width: 100%;
}

.template-menu {
  border: 1px solid lightgray;
  /*padding: 0.5em;*/
  overflow-y: scroll;
  height: 100%;
  max-height: 450px;
  width: 100%;
  min-width: 650px;
}

#template-form{
  width: 99%;
}

.submit-btn {
  /*position: absolute;
  bottom: 0.5em;*/
  border: none;
  background: lightblue;
  border-radius: 4px;
  height: 1.8em;
  font-size: 1.2em;
  margin-top: 0.5em;
}

.select-all-container {
  display: flex;
  width: 100%;
}

.select-all-container button {
  margin: 4px 4px;
  height: 1.6em;
  font-size: 1em;
}

.submit-btn:focus {
  outline: none;
}

#error-modal-container,
#console-modal-container,
#modal-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0,0,0,0.4);
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}

#error-modal-content,
#console-modal-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  max-height: 90%;
  min-height: 200px;
  width: 800px;
  background: #FFFFFF;
  position: relative;
  border: #22CCEB 3px solid;
  border-radius: 10px;
  padding: 10px;
  overflow-y: scroll;
}


#console-modal-content::-webkit-scrollbar-track {
  background: #FFFFFF;
  border-radius: 10px;
}

#console-modal-content::-webkit-scrollbar-thumb {
  color: #000000;
  border-radius: 10px;
}



.message {
  align-self: flex-start;
}

.invisible {
  display: none !important;
}

.modal-toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20px;
  width: 20px;
  border: 1px solid black;
  border-radius: 50%;
  position: absolute;
  right: 5px;
  top: 5px;
  cursor: pointer;
}



